const menu = () => {
    const menuItems = document.querySelectorAll('.menu__item'),
          gum = document.querySelector('.header__gum'),
          menuMobile = document.querySelector('.menu'),
          indexWrapper = document.querySelector('.index__girl');

    menuItems.forEach(item => {
        item.querySelector('.menu__no-hide').addEventListener('click', () => {
            menuItems.forEach(el => {
                if(item !== el) {
                    el.classList.remove('active');
                }
            })
            item.classList.toggle('active');
        })
        item.querySelector('.close').addEventListener('click', () => {
            menuItems.forEach(menu => {
                menu.classList.remove('active');
            })
        })
    })

    gum.addEventListener('click', () => {
        if(document.body.style.overflow == '') {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = '';
        }
        menuMobile.classList.toggle('active');
        gum.classList.toggle('active');
    })

    menuItems.forEach(item => {
        document.addEventListener('click', (e) => {
            if(item.querySelectorAll(`.${e.target.getAttribute('class')}`).length == 0 && item.querySelectorAll(`.${e.target.getAttribute('class')}`).length == 0) {
                item.classList.remove('active')
            }
        })
    })

}

export default menu;