const slider = () => {

    $('.girl__slider_wrapper').slick({
        variableWidth: true,
        centerMode: true,
        prevArrow: $('.girl__slider_arrow_prev'),
        nextArrow: $('.girl__slider_arrow_next'),
        dots: true,
        responsive: [
            {
              breakpoint: 1024,
              settings: {
                prevArrow: null,
                nextArrow: null,
              },
            },
          ]
    })

}

export default slider;