const search = () => {

    const searchItems = document.querySelectorAll('.search__item'),
          listItems = document.querySelectorAll('.list-item > li');

    searchItems.forEach(item => {
        const btn = item.querySelector('.search__item_right'),
              text = item.querySelector('.search__item_btn-close'),
              arrow = item.querySelector('.search__item_icon-close');

        btn.addEventListener('click', () => {
            if(!item.classList.contains('active')) {
                item.classList.add('active');
                text.textContent = 'Свернуть'
                arrow.style.transform = 'rotate(0deg)';
            } else {
                item.classList.remove('active');
                text.textContent = 'Развернуть'
                arrow.style.transform = 'rotate(180deg)';
            }
        })
    })

    listItems.forEach(item => {
        item.addEventListener('click', () => {
            item.classList.toggle('active')
        })
    })
}

export default search;