const cart = () => {

    const buttons = document.querySelectorAll('.cart__btn');

    buttons.forEach(item => {
        item.addEventListener('click', () => {
            buttons.forEach(el => {
                el.textContent = 'Показать телефон';
                
                const number = item.getAttribute('data-number');
                item.textContent = `${number}`;
            })
        })
    })
}

export default cart;